import ChallengePage from "./components/challengesPage/challengePage";
import Challenge from "./components/challenge/challenge";
import SignInPage from "./components/signIn/signInPage";
import SignUpPage from "./components/signUp/signUpPage";
import {
  BrowserRouter as Router,
  Routes,
  Route
} from "react-router-dom";

function App() {
  return (
    <Router>
        <Routes>
          <Route path="/code-in-one/" element={<ChallengePage />} />
          <Route path="/code-in-one/challenge/:id" element={<Challenge />} />
          <Route path="/code-in-one/login" element={<SignInPage />} />
          <Route path="/code-in-one/signup" element={<SignUpPage />} />
        </Routes>
    </Router>
  );
}

export default App;